.home {
  padding: 1rem;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: rgb(34, 193, 195);
  background: linear-gradient(
    115deg,
    rgba(34, 193, 195, 1) 0%,
    rgba(253, 187, 45, 1) 100%
  );
  width: 100%;
  height: 100%;
  overflow-x: hidden;
}

.home-text {
  padding: 1rem;
}

span {
  font-family: "PT Sans", sans-serif;
  font-size: 1.5rem;
  /* width: 70%; */
  padding: 0.2rem 0;
  display: block;
  font-family: monospace;
  white-space: nowrap;
  border-right: 4px solid;
  width: 61ch;
  animation: typing 6s steps(61), blink 0.6s infinite step-end alternate;
  overflow: hidden;
}

@keyframes typing {
  from {
    width: 0;
  }
}

@keyframes blink {
  50% {
    border-color: transparent;
  }
}

.buttons-container {
  margin: 2rem 0 3rem 0;
  display: flex;
  gap: 1rem;
  text-decoration: none;
}

.btn-explorer {
  padding: 0.3rem 0.5rem;
  background: transparent;
  font-size: 1rem;
  line-height: 1.5rem;
  text-align: center;
  letter-spacing: 0.39998px;
  border-radius: 4px;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 3px 1px -2px;
  background-color: #6f8f72;
  color: white;
  cursor: pointer;
  border: none;
}

.btn-favourites {
  padding: 0.2rem 0.5rem;
  background: transparent;
  font-size: 1rem;
  line-height: 1.5rem;
  text-align: center;
  letter-spacing: 0.39998px;
  border-radius: 4px;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 3px 1px -2px;
  background-color: transparent;
  color: white;
  cursor: pointer;
  border: 1px solid #6f8f72;
}

@media (max-width: 768px) {
  .home {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    margin: 0 2rem;
    width: 100%;
    height: 100vh;
  }

  .home-text {
    display: none;
  }

  .h1-title {
    font-size: 1.5rem;
  }
}
